* {
  box-sizing: border-box;
}
body {
  padding: 0;
  margin: 0;
  font-family: "Inter", sans-serif;
  font-weight: 300;
  font-size: 13px;
  line-height: 22px;
  color: #76746e;
}
ul {
  padding: 0;
  margin: 0;
  list-style: none;
}
.container {
  width: 100%;
  max-width: 576px;
  padding-left: 15px;
  padding-right: 15px;
  margin: 0 auto;
}
@media screen and (min-width: 768px) {
  .container {
    max-width: 1024px;
  }
}
@media screen and (min-width: 1200px) {
  .container {
    max-width: 1230px;
    padding-left: 30px;
    padding-right: 30px;
  }
}
.vis-hidd {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  /* IE6, IE7 */
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}
a {
  text-decoration: none;
  transition: all ease-in 0.3s;
}
a:active {
  opacity: 0.8;
}
.big-title {
  font-size: 37px;
  font-family: "Bellota Text", cursive;
  line-height: 49px;
  margin-top: 0;
  margin-bottom: 15px;
  color: #23211d;
}
@media screen and (min-width: 1200px) {
  .big-title {
    font-size: 39px;
    line-height: 62px;
  }
}
.form__fieldset {
  padding: 0;
  margin: 0;
  border: none;
}
.form__title {
  font-size: 20px;
  line-height: 29px;
  font-weight: bold;
  font-family: "Bellota Text", cursive;
  color: #23211d;
}
.form__text-input {
  background-color: #f9f7f7;
  border: none;
  border-radius: 3px;
  padding-top: 8px;
  padding-bottom: 9px;
  padding-left: 10px;
  padding-right: 10px;
}
.form__text-input::placeholder {
  color: #cac8c3;
}
.btn {
  font-weight: bold;
  font-size: 12px;
  line-height: 24px;
  display: inline-block;
  padding-top: 3px;
  padding-bottom: 3px;
  padding-left: 15px;
  padding-right: 15px;
  text-align: center;
  border-radius: 3px;
  transition: all ease-in 0.3s;
}
.btn--brown {
  border: 1px solid #c1bd98;
  background-color: #c1bd98;
  color: #ffffff;
}
.btn--brown:hover {
  background-color: #d6d09b;
  border: 1px solid #d6d09b;
}
.btn--brown:active {
  background-color: #9a9995;
  border: 1px solid #9a9995;
}
.btn--transparent {
  border: 1px solid #c1bd98;
  color: #c1bd98;
  background-color: transparent;
}
.btn--transparent:hover {
  background-color: #f9f7f7;
  border: 1px solid #f9f7f7;
}
.btn--transparent:active {
  background-color: #9a9995;
  border: 1px solid #9a9995;
}
.select2-container--default {
  border: none;
  margin-bottom: 10px;
  height: 32px;
}
.select2-container--default .selection {
  height: 32px;
}
.select2-container--default .select2-selection--single {
  background-color: #f9f7f7;
  border: none;
  color: #76746e;
  height: 32px;
}
.select2-container--default .select2-selection--single .select2-selection__rendered {
  color: #76746e;
  height: 32px;
  padding-top: 2px;
}
.select2-container--default .select2-selection--single .select2-selection__arrow {
  top: 3px;
}
.select2-container--default .select2-selection--single .select2-selection__arrow b {
  border: none;
  width: 11px;
  height: 7px;
  margin-left: -10px;
  background-image: url("../img/dropdown-arrow.png");
  background-size: cover;
  background-position: 50%;
  background-repeat: no-repeat;
}
.select2-container--open .select2-selection--single .select2-selection__arrow b {
  border: none;
  width: 11px;
  height: 7px;
  margin-left: -10px;
  background-image: url("../img/dropdown-arrow.png");
  background-size: cover;
  background-position: 50%;
  background-repeat: no-repeat;
  transform: rotate(180deg);
}
.select2-results__option {
  background-color: #f9f7f7;
  border-radius: 4px;
}
.select2-container--default .select2-results__option--highlighted.select2-results__option--selectable {
  background-color: #c1bd98;
}
.select2-dropdown {
  border: 1px solid #d9d8d4;
  border-radius: 4px;
}
.select2-dropdown .select2-dropdown--below {
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
}
.select2-container--default .select2-search--dropdown .select2-search__field {
  border-radius: 3px;
  border: 1px solid #c1bd98;
}
