.header__mobile-btn {
  height: 29px;
  width: 30px;
  position: absolute;
  top: 12px;
  right: 15px;
  background-color: transparent;
  border: none;
  outline: none;
}
@media screen and (min-width: 1200px) {
  .header__mobile-btn {
    display: none;
  }
}
.header__mobile-btn--gamburger {
  animation: scale 0.3s;
  height: 29px;
  width: 30px;
}
.header__mobile-btn--gamburger::after {
  content: "";
  display: block;
  width: 100%;
  height: 3px;
  background-color: #c1bd98;
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 1px;
}
.header__mobile-btn--gamburger::before {
  content: "";
  display: block;
  width: 100%;
  height: 3px;
  background-color: #c1bd98;
  position: absolute;
  bottom: 0;
  left: 0;
  border-radius: 1px;
}
.header__mobile-btn--gamburger .header__mobile-btn-decor {
  display: block;
  width: 80%;
  height: 3px;
  background-color: #c1bd98;
  position: absolute;
  top: 13px;
  right: 0;
  border-radius: 1px;
}
.header__mobile-btn--close {
  animation: scale2 0.4s;
}
.header__mobile-btn--close::after {
  content: "";
  display: block;
  width: 31px;
  height: 3px;
  background-color: #c1bd98;
  position: absolute;
  top: 13px;
  left: 0;
  transform: rotate(45deg);
  border-radius: 1px;
}
.header__mobile-btn--close::before {
  content: "";
  display: block;
  width: 31px;
  height: 3px;
  background-color: #c1bd98;
  position: absolute;
  bottom: 13px;
  left: 0;
  transform: rotate(135deg);
  border-radius: 1px;
}
.header__mobile-btn--close .header__mobile-btn-decor {
  display: none;
}
@keyframes scale {
  0% {
    transform: scale(0);
  }
  70% {
    transform: scale(1.2);
  }
  100% {
    transform: scale(1);
  }
}
@keyframes scale2 {
  0% {
    transform: scale(0);
  }
  70% {
    transform: scale(1.2);
  }
  100% {
    transform: scale(1);
  }
}
